body {
  color: #fff;
  background-color: #282c36;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.timer {
  background: #3a3f4b;
  border-radius: 10px;
